import React from "react";
import styles from "./UseStatsGraphs.module.css";
import { VictoryPie, VictoryChart, VictoryBar } from "victory";

const UseStatsGraphs = ({ data }) => {
  const [graph, setGraph] = React.useState([]);
  const [total, setTotal] = React.useState(0);

  React.useEffect(() => {
    const graphData = data.map(({ title, acessos }) => {
      return {
        x: title,
        y: Number(acessos),
      };
    });

    const totalAcesso = data.map(({ acessos }) => Number(acessos));
    if (totalAcesso > 0) setTotal(totalAcesso.reduce((a, b) => a + b));
    setGraph(graphData);
    console.log(data);
  }, [data]);

  return (
    <section className={`animaLeft ${styles.graph}`}>
      <div className={` ${styles.total} ${styles.graphItem}`}>
        <p>Acessos: {total}</p>
      </div>
      <div className={styles.graphItem}>
        <VictoryPie
          data={graph}
          innerRadius={50}
          padding={{ top: 20, bottom: 20 }}
          style={{
            data: {
              fillOpacity: 0.9,
              stroke: "#ffff",
              strokeWidth: 2,
            },
            labels: {
              fontSize: 14,
              fill: "#333",
            },
          }}
        />
      </div>
      <div className={styles.graphItem}>
        <VictoryChart>
          <VictoryBar alignment="start" data={graph}></VictoryBar>
        </VictoryChart>
      </div>
    </section>
  );
};

export default UseStatsGraphs;
